<template>
  <div>
    <!--面包屑导航区域-->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item><a href="/">应用管理</a></el-breadcrumb-item>
      <el-breadcrumb-item>修改参数</el-breadcrumb-item>
    </el-breadcrumb>
    <!--卡片视图区-->
    <el-card>
      <div>
        <el-tabs v-model="infoModify" @tab-click="modifyInformation">
          <el-tab-pane label="App基本信息修改" name="first">
            <div style="margin-top: 15px; width: 30%">
              <el-input placeholder="请输入内容" v-model="appName" >
                <el-button icon="el-icon-search" @click="searchAppByName" slot="append"></el-button>
              </el-input>
            </div>
            <el-row :gutter="30">
              <el-col :span="10" >
                <el-card :data="basicInfo">
                  <div>
                    <span style="margin-bottom: 40%">基本信息</span>
                    <el-button style="float: right; padding: 3px 0" type="text" @click="basicInfoChange">修改信息</el-button>
                  </div>
                  <el-divider content-position="center"><el-icon class="el-icon-data-analysis"></el-icon></el-divider>
                  <span class="basicInfoTitle">名称: </span>
                  <p class="basicInfoContent">{{basicInfo.name}}</p>
                  <el-divider></el-divider>
                  <span class="basicInfoTitle">开发者: </span>
                  <p class="basicInfoContent">{{basicInfo.author}}</p>
                  <el-divider></el-divider>
                  <span class="basicInfoTitle">链接: </span>
                  <p class="basicInfoContent">{{basicInfo.url}}</p>
                  <el-divider></el-divider>
                  <span class="basicInfoTitle">基本信息说明: </span>
                  <p class="basicInfoContent">{{basicInfo.basicInformation}}</p>
                </el-card>
              </el-col>
              <el-col :span="2">
                <div></div>
              </el-col>
              <el-col :span="12">
                <div>
                  <el-collapse v-model="editBasicInfo" @change="handleChange">
                    <el-collapse-item title="名称 Name" name="1" class="editInfoTitle">
                      <div>
                        <el-input v-model="editInfo.name" prefix-icon="el-icon-s-check" class="editInfo"></el-input>
                      </div>
                    </el-collapse-item>
                    <el-collapse-item title="开发者 Developer" name="2">
                      <div>
                        <el-input v-model="editInfo.author" prefix-icon="el-icon-user-solid"></el-input>
                      </div>
                    </el-collapse-item>
                    <el-collapse-item title="链接 URL" name="3">
                      <el-input v-model="editInfo.url" prefix-icon="el-icon-platform-eleme" disabled></el-input>
                    </el-collapse-item>
                    <el-collapse-item title="基本信息说明 Basic Information" name="4">
                      <el-input v-model="editInfo.basicInformation" prefix-icon="el-icon-info"></el-input>
                    </el-collapse-item>
                  </el-collapse>
                  <el-button type="info" @click="reEditInfo" size="small" class="editInfoButton1">重置</el-button>
                  <el-button type="primary" @click="editInfoSend" size="small" class="editInfoButton2">确定</el-button>
                </div>
              </el-col>
            </el-row>
          </el-tab-pane>
          <el-tab-pane label="App版本信息修改" name="second">
            <el-row :gutter="30">
              <el-col :span="7" >
                <div class="block">
                  <el-timeline>
                    <!--// 从数据库中获取版本更新数据-->
                    <el-timeline-item
                      v-for="(version, index) in allVersion" :key="index" :timestamp="version.time">
                      <h2>{{version.name + version.versionInformation}}</h2>
                      <p>版本号: {{version.versionId}}</p>
                    </el-timeline-item>
                  </el-timeline>
                </div>
              </el-col>
              <el-col :span="17">
                <div>
                  <!--// 绑定的是versionInfo的数据-->
                  <el-table :data="allVersion" style="width: 90%" ref="multipleTable" display="flex">
                    <el-table-column type="index"></el-table-column>
                    <el-table-column prop="name" label="名称" >
                    </el-table-column>
                    <el-table-column prop="versionId" label="版本号">
                    </el-table-column>
                    <el-table-column prop="time" label="上传时间" width="110px">
                    </el-table-column>
                    <el-table-column label="操作">
                      <el-button type="text" @click="editDialogVisible = true">编辑</el-button>
                    </el-table-column>
                  </el-table>
                </div>
              </el-col>
            </el-row>
            <el-dialog title="修改版本信息" :visible.sync="editDialogVisible" width="50%" @close="editDialogClose">
              <el-form ref="editVersionRef" label-width="100px" :model="editVersion" :rules="editFormRules"
              label-position="right">
                <el-form-item label="应用名称" ::before="0">
                  <el-input v-model="editVersion.name" disabled>
                  </el-input>
                </el-form-item>
                <el-form-item label="版本" >
                  <el-input v-model="editVersion.version"></el-input>
                </el-form-item>
                <el-form-item label="版本信息说明" prop="versionInfo">
                  <el-input class="input" v-model="editVersion.versionInfo" type="textarea" autosize></el-input>
                </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button type="info" @click="editDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="editVersionInfo">确 定</el-button>
              </div>
            </el-dialog>
          </el-tab-pane>
        </el-tabs>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 选项卡记录
      infoModify: 'first',
      basicInfo: {
        basicInformation: '',
        name: '',
        url: '',
        author: ''
      },
      editBasicInfo: [],
      editInfo: {
        basicInformation: '',
        name: '',
        url: '',
        author: ''
      },
      // 获取所有app版本更新信息
      allVersion: [
        {
          name: 'aaa',
          versionInformation: '发布',
          versionId: '1.0.x',
          time: '2020-02-03'
        },
        {
          name: 'ccc',
          versionInformation: '下架',
          versionId: '1.0.x',
          time: '2020-02-03'
        },
        {
          name: 'bbb',
          versionInformation: '修改基础信息',
          versionId: '1.0.x',
          time: '2020-02-03'
        }],
      /* // 获取版本信息渲染到表格中
      versionInfo: [
        {
          activity: '发布',
          name: 'aaa',
          versionInformation: 'asdfsasdfasdf',
          versionId: '1.0.x',
          time: '2020-02-03'
        }
      ], */
      editDialogVisible: false,
      editVersion: {
        name: '',
        version: '',
        versionInfo: '',
        time: ''
      },
      editFormRules: {
        versionInfo: [
          { min: 4, message: '请输入至少4个字符', trigger: 'blur' }
        ]
      },
      appName: ''
    }
  },
  created () {
    this.getAllVersion()
  },
  methods: {
    async getAllVersion () {
      const { data: res } = await this.$http.get('version', { params: this.$store.state.user.username })
      if (res.code !== 200) {
        return this.$message.error('获取版本信息列表失败')
      }
      this.allVersion = res.data
    },
    handleChange () {
      console.log(this.selectedCate)
    },
    // 选择修改信息的类型
    modifyInformation () {
      console.log(this.infoModify)
    },
    // 展开修改基础信息的页面
    basicInfoChange () {
      this.editBasicInfo = ['1', '2', '3', '4']
    },
    // 重置基础信息修改的表单显示
    reEditInfo () {
      this.editInfo = {
        basicInformation: '',
        name: '',
        url: '',
        author: ''
      }
    },
    // 发送基础修改信息到数据库
    editInfoSend () {
      console.log(this.editInfo)
      const { data: res } = this.$http.post('cAppBasic', { params: {
        basicInformation: this.editInfo.basicInformation, name: this.editInfo.name, author: this.editInfo.author } })
      if (res.code !== 200) return this.$message.error('修改信息失败')
      this.$message.success('修改信息成功')
      this.editInfo.url = this.basicInfo.url
      this.basicInfo = this.editInfo
      this.editBasicInfo = ['1', '2', '3', '4']
    },
    editDialogClose () {
      this.$refs.editVersionRef.resetFields()
    },
    // 发送版本信息修改到数据库
    editVersionInfo () {
      console.log(this.editVersion)
      const { data: res } = this.$http.post('cAppVersion', { params: {
        versionInformation: this.editVersion.versionInfo, version: this.editVersion.version } })
      if (res.code !== 200) return this.$message.error('修改信息失败')
      this.$message.success('修改信息成功')
      this.editDialogVisible = false
    },
    // 查找应用
    searchAppByName () {
      console.log(this.appName)
      const { data: res } = this.$http.post('', { params: { appName: this.appName, developer: this.$store.state.user.username } })
      if (res.code !== 200) return this.$message.error('查找应用失败')
      this.basicInfo = res.data
    }
  }
}
</script>

<style lang="less" scoped>
  .category {
    margin: 15px 0
  }
  .editInfoButton1 {
    margin: 15px;
    transform: translate(300%, 0);
  }
  .editInfoButton2 {
    margin: 15px;
    transform: translate(300%, 0);
  }
  .versionBtn {
    margin: 15px;
    transform: translate(300%, 0);
  }
  .el-form-item {
    padding-inline: 0;
    margin-bottom: -20px;
  }
  .el-input {
    transform: translate(0, -15px);
  }
  .input {
    transform: translate(0, -18px);
  }
</style>
