<template>
  <div>
    <!--面包屑导航区域-->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item><a href="/">应用管理</a></el-breadcrumb-item>
      <el-breadcrumb-item>应用列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!--卡片视图区-->
    <el-card>
      <el-row :gutter="10">
        <el-col :span="4">
          <!--v-model="appModifyList"-->
          <el-select v-model="select" slot="prepend" placeholder="请选择" style="width: 100px;">
            <el-option label="待上架" value="1"></el-option>
            <el-option label="待修改" value="2"></el-option>
            <el-option label="待下架" value="3"></el-option>
          </el-select>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goAddPage">添加应用</el-button>
        </el-col>
      </el-row>
      <el-table :data="appLists" empty-text="暂无需要审核的应用">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="应用Id">
                <span>{{ props.row.id }}</span>
              </el-form-item>
              <el-form-item label="应用类型">
                <span>{{ props.row.types }}</span>
              </el-form-item>
              <el-form-item label="超链接">
                <span>{{ props.row.appBasic.url }}</span>
              </el-form-item>
              <el-form-item label="更新时间">
                <span>{{ props.row.appVersion.updateTime }}</span>
                <!--<el-table-column label="上架时间">
                  <template slot-scope="scope">{{scope.row.add_time | dateFormat}}</template>
                </el-table-column>-->
              </el-form-item>
              <el-form-item label="基础信息说明">
                <span>{{ props.row.appBasic.basicInformation }}</span>
              </el-form-item>
              <el-form-item label="版本信息说明">
                <span>{{ props.row.appVersion.versionInformation }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column label="应用名称" prop="appName">
        </el-table-column>
        <el-table-column label="应用作者" prop="appBasic.author">
        </el-table-column>
        <el-table-column label="应用版本" prop="appVersion.versionId">
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" icon="el-icon-upload2" @click="on(scope.row)"></el-button>
            <el-button type="info" size="mini" icon="el-icon-download" @click="down(scope.row)"></el-button>
            <el-button type="warning" size="mini" icon="el-icon-delete" @click="remove(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination :total="total" layout="total">
      </el-pagination>
      <!--<el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>-->
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      appModifyList: '',
      select: '',
      appLists: [{
        id: '001',
        appName: 'WeChat',
        types: '聊天',
        appBasic: {
          id: '001',
          name: 'WeChat',
          author: 'Tencent',
          state: '0', // 上下架
          basicInformation: '微信',
          url: 'www.dev.com',
          examine: '0' // 审核情况
        },
        appVersion: {
          id: '001',
          name: 'WeChat',
          versionId: '7.0.10',
          versionInformation: '现已完成漏洞修补',
          updateTime: '2020-02-02 15:24:33'
        }
      }],
      total: 1
    }
  },
  created () {
    this.getGoodsList()
  },
  methods: {
    async getGoodsList () {
      const { data: res } = await this.$http.get('apps', { params: this.$store.state.user.username })
      if (res.meta.status !== 200) return this.$message.error('获取数据失败')
      console.log(res)
      this.appLists = res.data
      this.total = res.data.length
      this.$message.success('获取数据成功')
    },
    goAddPage () {
      this.$router.push('/apps/add')
    },
    async on (app) {
      const { data: res } = await this.$http.post('on', { params: { name: app.appName, id: app.id } })
      if (res.code !== 200) return this.$message.error('应用上架失败')
      this.getGoodsList()
      return this.$message.success('应用上架成功')
    },
    async down (app) {
      const { data: res } = await this.$http.post('down', { params: { name: app.appName, id: app.id } })
      if (res.code !== 200) return this.$message.error('应用下架失败')
      this.getGoodsList()
      return this.$message.success('应用下架成功')
    },
    remove (app) {
      this.$confirm('此操作将永久删除应用, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const { data: res } = this.$http.post('delete', { params: app.appName })
        if (res.code !== 200) return this.$message.error('应用删除失败')
        this.getGoodsList()
        return this.$message.success('应用删除成功')
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    }
  }
}
</script>

<style lang="less" scoped>
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>
